<script>
import config from "../../../../public/config/customSetting.json";
export default {
  name: "historyAnnouncementDetail-component",
  data() {
    return {};
  },
  computed: {
    bbsHistoryDetail() {
      return this.$store.state.bbsHistory_detail;
    },
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    // 從對應 controller 下載文件，並指定檔名
    async downloadPdf(item) {
      try {
        const res = await this.$store.dispatch("commonDownloadPdf", {
          file_id: item.guid,
          router_Name: config.getBoard,
        });
        this.downloadFile(res, item.original_Name, "pdf");
      } catch (err) {
        console.log(err);
      }
    },
  },
  async mounted() {
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;

    // 打 API 取得該筆明細
    const bbsNo = this.$route.params.bbs_No;
    try {
      await this.$store.dispatch("getBbsHistoryDetail", {
        bbsNo: bbsNo,
      });
    } catch (err) {
      console.log(err);
    }
  },
};
</script>

<template src="./historyAnnouncementDetail.html"></template>
